.backBlock{
    cursor: default;
    position: relative;
    left: 30px;
    top: 20px;
}


.profileBlock{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 393px;
    height: 360px;
    margin: 120px auto;
    background-color: #FFFFFF;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    padding: 20px;
}
.btnLogAut {
    margin-top: 25px;

}
s.btnIcon{
    color: #000000;
}
s.btnText{
    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.01em;
    color: #000000;
    opacity: 0.8;
}
.avatarContainer {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: relative;
    padding-top: 30px;
}
.avatarContainer img {
    max-width: 100%;
}
.iconAvatar {
    background-color: #808080;
    color: #ffffff;
    border: 2px solid #808080;
    border-radius: 50%;
    position: absolute;
    top: 93px;
    left: 70px;
    width: 100px;
    height: 100px;
}
.nameContainer{
    padding-top: 17px;
    display: flex
}

.title{
    padding-top: 27px;
    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
}
.iconName{
    color: #000000;
}



.emailText{
    padding-top: 14px;
    font-family: 'Montserrat', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    opacity: 0.5;
}